.header {
    position: fixed;
    top: 0;
    left: 0;
    padding: 20px 50px 0 50px;
    width: 100vw;
    height: 65px;
    background-color: rgb(0, 0, 0);
    box-sizing: border-box;
    z-index: 3;
}

.header__navigation {
    display: flex;
    width: 35%;
    height: 100%;
    justify-content: space-between;
    align-items: center;
}

.header__link-container {
    cursor: pointer;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.header__text {
    font-size: 20px;
    position: relative;
    text-decoration: none;
    color: rgba(248, 248, 248, 0.671);
    transition: color 0.5s;
}

.header__line {
    width: 0;
    transition: width 0.5s;
    border-bottom: 2px solid red;
}
.header__text:hover,
.header__text:hover ~ .header__line {
    color: white;
    width: 100%;
}
.header__text.active,
.header__text.active + .header__line {
    width: 100%;
    color: white;
}

.header__burger {
    display: none;
    position: relative;
    cursor: pointer;
    width: 40px;
    height: 25px;
}

.header__burger > span {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 5px;
    background: white;
    transform: scale(1, 0.97);
    transition: transform 0.3s;
}
.header__burger::after {
    content: "";
    top: 100%;
    width: 100%;
    height: 5px;
    background: white;
    position: absolute;
    transform: rotate(0deg);
    transition: transform 0.3s;
}

.header__burger::before {
    content: "";
    top: 0;
    width: 100%;
    height: 5px;
    background: white;
    position: absolute;
    transform: rotate(0deg);
    transition: transform 0.3s;
}

.header__burger_disable > span {
    transform: scale(0, 0);
}

.header__burger_disable::before {
    top: 50%;
    transform: rotate(-45deg);
}

.header__burger_disable::after {
    top: 50%;
    transform: rotate(45deg);
}

@media screen and (max-width: 1100px) {
    .header__text {
        font-size: 16px;
    }
}

@media screen and (max-width: 880px) {
    .header {
        padding: 20px 0 0 0;
        display: flex;
        justify-content: flex-end;
    }
    .header__navigation {
        display: none;
    }
    .header__burger {
        margin-right: 30px;
        display: block;
    }
}
