.main {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main__image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-image: url("../../images/—Pngtree—business pure white background_359687.jpg");
    background-repeat: no-repeat;
    background-size: 200%;
    animation: move 220s infinite;
}

.main__text-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.main__text {
    font-size: 18px;
    padding: 10px;
    color: white;
    background: rgba(5, 5, 5, 0.486);
    border: 2px solid white;
    border-radius: 10px;
}

.main__link {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border: 2px solid black;
    width: 300px;
    height: 65px;
    color: black;
    font-size: 40px;
    text-decoration: none;
    background: transparent;
    transition: background 0.3s;
}

.main__link > span {
    transform: rotate(0deg);
    transition: transform 0.3s;
}
.main__link:hover {
    background: rgb(201, 23, 0);
    border: 2px solid white;
    color: white;
}
.main__link:hover > span {
    transform: rotate(90deg);
}

@media screen and (max-width: 880px) {
    .main__image {
        background-size: 400%;
    }
    .main__text-container {
        width: 90%;
    }
}

@media screen and (max-width: 475px) {
    .main__image {
        background-size: 500%;
    }
    .main__link {
        width: 250px;
        font-size: 34px;
    }
}
