.menu {
    position: fixed;
    display: flex;
    justify-content: flex-end;
    top: 65px;
    width: 100vw;
    height: 100vh;
    background: rgba(2, 2, 2, 0.342);
    overflow: hidden;
}

.menu__nav {
    justify-content: space-around;
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 50%;
    background: black;
    border-bottom-left-radius: 10px;
    transform: translate(100%, 0);
    transition: all 0.3s;
}
.open {
    transform: translate(0, 0);
}

.menu.open {
    visibility: hidden;
}
.menu__link-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 15px;
}

.menu__text {
    font-size: 20px;
    position: relative;
    text-decoration: none;
    color: rgba(248, 248, 248, 0.671);
    transition: color 0.5s;
}

.menu__line {
    width: 0;
    transition: width 0.5s;
    border-bottom: 2px solid red;
}

.menu__text.active {
    color: white;
    font-size: 24px;
}
.menu__text.active + .menu__line {
    width: 100%;
}

@media screen and (max-width: 475px) {
    .menu__nav {
        width: 100%;
        height: 100%;
    }
}
